@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Mono&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.weather-container {
  height: calc(100vh);
  width: calc(100vw);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

button {
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: transparent;
  border: none;
  font-size: 25px;
  padding: 10px;
}

button:hover {
  border: 1px solid rgba(0, 0, 0);
  border-radius: 16px;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 35px;
  font-family: "Noto Sans Mono", monospace;
  text-align: center;
  position: absolute;
  background: rgba(255, 255, 255, 0.35);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.input {
  padding: 10px;
  background: rgba(255, 255, 255, 0.35);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  font-family: "Noto Sans Mono", monospace;
  color: rgb(0, 0, 0);
}

.bg-img {
  height: 120vh;
  width: 120vw;
  position: relative;
  z-index: -1;
}

.card-text {
  margin-top: 5px;
}
